@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
.App {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  body {
    padding: 0;
    margin: 0;
  }

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Poppins', sans-serif;
    background-color: #f9f9f9;
    color: #333;
    scroll-behavior: smooth;
  }
  
  
.navbar {
  width: 100%;
  height: 100px;
  background-color: #121619;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  position: sticky;
  top: 0;
  z-index: 1000;
  animation: fadeIn 1s ease-in;
}

.navbar .leftSide img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.navbar .leftSide img:hover {
  transform: scale(1.1);
}

.navbar .rightSide {
  display: flex;
  grid-gap: 25px;
  gap: 25px;
  font-family: Arial, Helvetica, sans-serif;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  position: relative;
  transition: color 0.3s ease;
}

.navbar a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: #f8c630;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  transform-origin: right;
}

.navbar a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}


.navbar .rightSide {
  font-family: 'Poppins', sans-serif;
}
.navbar a {
  font-family: 'Poppins', sans-serif;
}

.footer {
  width: 100%;
  height: 150px;
  background-color: #121619;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  animation: fadeIn 1.5s ease-in;
}

.socialMedia svg {
  color: white;
  margin: 10px;
  font-size: 30px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.socialMedia svg:hover {
  color: #f8c630;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.footer {
  font-family: 'Poppins', sans-serif;
}

.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  animation: fadeIn 1s ease-out;
  transition: background 0.5s ease-in-out;
}

.headerContainer {
  width: auto;
  margin-left: 50px;
}

.headerContainer h1 {
  font-size: 90px;
  margin: 0;
  padding-bottom: 20px;
  font-weight: 600;
  color: black;
  line-height: 1.2;
}

.headerContainer p {
  margin: 0;
  padding-bottom: 20px;
  font-size: 40px;
  font-weight: lighter;
  color: black;
}

.headerContainer button {
  margin-top: 10px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #121619;
  height: 50px;
  width: 180px;
  text-decoration: none;
  font-size: 20px;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.headerContainer button:hover {
  background-color: #07090a;
  transform: scale(1.05);
}

@media only screen and (max-width: 650px) {
  .home {
    justify-content: center;
    align-items: center;
  }

  .headerContainer {
    margin-left: 0px;
    border-radius: 10px;
    padding: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121619;
    color: white;
  }

  .headerContainer h1 {
    font-size: 40px;
    height: auto;
    margin-bottom: 10px;
    color: white;
  }

  .headerContainer p {
    font-size: 30px;
    margin-bottom: 15px;
    color: white;
  }

  .headerContainer button {
    background-color: white;
    color: #121619;
    margin-top: 10px;
    margin-bottom: 30px;
  }

  .headerContainer button:hover {
    background-color: rgb(225, 225, 225);
    color: #121619;
  }
}



.headerContainer h1 {
  font-family: 'Playfair Display', serif;
}
.headerContainer p {
  font-family: 'Open Sans', sans-serif;
}
.headerContainer button {
  font-family: 'Poppins', sans-serif;
}

.menu {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: fadeIn 1s ease-in;
}

.menu .menuTitle {
  font-family: cursive;
  font-size: 60px;
  margin-bottom: 20px;
}

.menuList {
  width: 70vw;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
  place-items: center;
  animation: slideIn 1s ease-in;
}

.menuItem {
  border-radius: 15px;
  width: 300px;
  height: 350px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease;
  animation: slideUp 1s ease-in;
}

.menuItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.menuItem div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.menuItem h1,
.menuItem p {
  margin-left: 20px;
}

@media only screen and (max-width: 1300px) {
  .menuList {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .menuList {
    grid-template-columns: 1fr;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}


.menu .menuTitle {
  font-family: 'Playfair Display', serif;
}
.menuItem h1,
.menuItem p {
  font-family: 'Open Sans', sans-serif;
}

.about {
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  animation: fadeIn 1s ease-in-out;
}

.aboutTop {
  width: 100%;
  height: 45%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.aboutBottom {
  display: flex;
  flex-direction: column;
  height: 55%;
  align-items: center;
  margin-top: 50px;
}

.about .aboutBottom h1 {
  font-weight: 600;
  font-size: 70px;
  color: black;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  transform: translateY(-40px);
  animation: slideIn 1.2s ease-in-out;
}

.aboutBottom p {
  width: 1200px;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 1.8;
  color: #333;
  text-align: center;
  animation: fadeIn 1.4s ease-in;
}


.about .aboutBottom h1 {
  font-family: 'Playfair Display', serif;
  font-size: 70px;
  font-weight: 700;
  color: black;
}
.aboutBottom p {
  font-family: 'Open Sans', sans-serif;
}

.contact {
  width: 100%;
  height: 90vh;
  display: flex;
  animation: fadeIn 1.2s ease-in;
}

.contact .leftSide {
  height: 100%;
  flex: 50% 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: slideInLeft 1s ease-out;
}

.contact .rightSide {
  height: 100%;
  flex: 50% 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: slideInRight 1s ease-out;
}

.contact .rightSide h1 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: 10;
  font-size: 50px;
  margin-left: 30px;
}

form {
  display: flex;
  flex-direction: column;
  width: auto;
  height: auto;
  padding: 30px;
  animation: fadeIn 1.4s ease-in;
}

form input,
form textarea {
  height: 40px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
  margin-bottom: 15px;
  font-family: Arial, Helvetica, sans-serif;
}

form textarea {
  height: 70px;
}

input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
}

form input:focus,
textarea:focus {
  outline: none;
}

form label {
  margin-top: 15px;
  color: grey;
}

form button {
  margin-top: 20px;
  width: 140px;
  height: 40px;
  border: none;
  background-color: #121619;
  color: whitesmoke;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #1f1f1f;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}


.contact .rightSide h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}
form input,
form textarea {
  font-family: 'Open Sans', sans-serif;
}
form label {
  font-family: 'Poppins', sans-serif;
}
form button {
  font-family: 'Poppins', sans-serif;
}

